.advantage__wrap {
    background-color: $light-pink;
    padding: 60px 0;
    margin-top: 130px;
}

.advantage__items {
    display: flex;
    flex-wrap: wrap;
    gap: 40px;
    justify-content: space-around;
}

.advantage__item {
    display: grid;
    gap: 30px;
    text-align: center;

    h4 {
        position: relative;
        font-weight: 700;
        font-size: px-to-rem(56px);
        line-height: px-to-rem(70px);
    }

    h4::after {
        content: "";
        background-color: #222;
        position: absolute;
        width: 90%;
        height: 3px;
        left: 0;
        right: 0;
        margin: auto;
        bottom: -5%;
    }

    p {
        font-size: px-to-rem(24px);
        line-height: px-to-rem(30px);      
    }
}

@media (max-width:1024px) {

    .advantage__wrap {
        margin-top: 100px;
    }

    .advantage__items {
        justify-content: space-around;

        h4 {
            font-size: px-to-rem(45px);
            line-height: px-to-rem(50px);
        }

        h4::after {
            width: 70%;
        }
    }
}