@mixin clearfix() {
  &:before,
  &:after {
    content: '';
    display: table;
    clear: both;
  }
}

@mixin min-max($min-width, $max-width, $padding) {
  min-width: $min-width;
  max-width: $max-width;
  padding: 0 $padding;
  box-sizing: border-box;
  margin: 0 auto;
}

@mixin adaptive-video() {
  position: relative;
  padding-bottom: 56.25%; /* задаёт высоту контейнера для 16:9 (если 4:3 — поставьте 75%) */
  height: 0;
  overflow: hidden;

  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

}

@mixin visually-hidden() {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0,0,0,0);
  overflow: hidden;
}

@function px-to-rem($size, $basicSize: 16px) {
  @return $size / $basicSize+#{rem};
}

@mixin dynamicText($minSizePercent, $maxSize, $minSize, $minWidthPx, $maxWidth, $minWidth) {
  font-size: calc(#{$minSizePercent} + (#{$maxSize} - #{$minSize}) * (100vw - #{$minWidthPx}) / (#{$maxWidth} - #{$minWidth}));
}

@mixin input() {
  font-size: px-to-rem(14px);
	color: $gray;
  padding: 14px 16px;
  border: none;
}


@mixin button ($bg-and-border-color: $font-color-brown, $font-color: #ccc, $border: false){
  transform: translateX(0) translateY(0);
  padding: 16px 32px;
  width: max-content;
  font-weight: 500;
  border: 0;
  display: block;
  font-size: px-to-rem(18px);
  border-radius: 0;
  transition: .6s;
  overflow: hidden;
  @if $border == true {
        border: 1px solid $bg-and-border-color;
        color: $bg-and-border-color
      } @else if $border == false {
        background-color: $bg-and-border-color;
        color: $font-color;
      }
  &:focus{
    outline: 0;
  }
  &:before{
    @if $border == false { 
    content: '';
    display: block;
    position: absolute;
    background: rgba(255,255,255,0.5);
    width: 60px;
    height: 100%;
    left: 0;
    top: 0;
    opacity: .5;
    filter: blur(30px);
    transform: translateX(-100px)  skewX(-15deg);
    }
  }
  &:after{
    @if $border == false { 
    content: '';
    display: block;
    position: absolute;
    background: rgba(255,255,255,0.2);
    width: 30px;
    height: 100%;
    left: 30px;
    top: 0;
    opacity: 0;
    filter: blur(5px);
    transform: translateX(-100px) skewX(-15deg);
    }
  }
  &:hover {
    text-decoration: none;
    
    @if $border == true {
      border: 1px solid $light-pink;
    }

    @else if $border == false {
      
      background: $light-pink;
      color: $font-color-brown;
      cursor: pointer;
      &:before{
        transform: translateX(300px)  skewX(-15deg);  
        opacity: 0.6;
        transition: .7s;
      }
      &:after{
        transform: translateX(300px) skewX(-15deg);  
        opacity: 1;
        transition: .7s;
      }
    }
  }
}