.offer__slide {
    position: relative;

    .banner__img {
        width: 100%;
        height: 100%;
    }

    img {
        width: 100%;
        height: 700px;
        object-fit: cover;
        object-position: center right;
    }

    .offer__slide-text {
        position: absolute;
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        gap: 30px;
        height: max-content;
        // top: 10%;
        left: 5%;
        right: 5%;
        top: 0;
        bottom: 0;
        margin: auto;
    
        h2 {
            font-weight: 500;
            max-width: 620px;
            font-size: px-to-rem(56px);
            line-height: px-to-rem(70px);
        }
    
        p {
            font-size: px-to-rem(20px);
            line-height: px-to-rem(32px);
            max-width: 516px;
            color: $gray2; 
        }
    
    }
}
.swiper-pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}
.swiper-pagination-bullet {
    height: 2px;
    width: 60px;
    border-radius: 0;
    background: #000;
    margin: 0 10px;
}

.swiper-pagination-bullet.swiper-pagination-bullet-active {
    height: 3px;
}

.swiper-pagination-horizontal {
    span, 
    span:active, 
    span:focus {
        outline: none;
    }
}

@media (min-width:1700px) {

    .offer__slide {
        .offer__slide-text {
            left: 15%;
        }
    }

}

@media (max-width:1024px) {

    .offer__slide {
        display: grid;
        grid-template-rows: (max-content) auto;
        align-items: flex-start;

        img {
            object-position: center center;
            width: 100%;
            height: 100%;
        }
    
        .offer__slide-text {
            position: relative;
            top: 0;
            left: 0;
            // background-color: $bg-color-tin;
            padding: 60px 30px;
            text-align: center;

            h2 {
                max-width: 100%;
                font-size: px-to-rem(36px);
                line-height: px-to-rem(50px);
            }

            p {
                max-width: 600px;
                font-size: px-to-rem(20px);
                line-height: px-to-rem(28px);
            }

            .btn {
                margin: 0 auto;
            }
        }
    }
}
