@charset "UTF-8";

/**
 * Основные стили
 **/

html {
	font-size: 16px;
	color: $font-color-brown;
}

a {
	text-decoration: none;
	color: $black;

	&:hover {
        text-decoration: underline;
    }
}

body {
	font-family: 'Red Hat Display', sans-serif;	
	overflow-wrap: break-word;
	word-wrap: break-word;
	font-weight: 400;
}

.btn {
    @include button();
}

input {
	@include input();
}

.container {
	@include min-max(320px, 100%, 0);
}

.wrap {
	@include min-max(320px, 1400px, 15px);
}