@charset "UTF-8";
/* Указываем box sizing */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Убираем внутренние отступы */
ul[class],
ol[class] {
  padding: 0;
}

/* Убираем внешние отступы */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0;
}

/* Выставляем основные настройки по-умолчанию для body */
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.3;
}

/* Удаляем стандартную стилизацию для всех ul и il, у которых есть атрибут class*/
ul[class],
ol[class] {
  list-style: none;
}

/* Упрощаем работу с изображениями */
img {
  max-width: 100%;
  display: block;
}

/* Наследуем шрифты для инпутов и кнопок */
input,
button,
textarea,
select {
  font: inherit;
}

/**
 * Основные стили
 **/
html {
  font-size: 16px;
  color: #222222;
}

a {
  text-decoration: none;
  color: #000;
}
a:hover {
  text-decoration: underline;
}

body {
  font-family: "Red Hat Display", sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word;
  font-weight: 400;
}

.btn {
  transform: translateX(0) translateY(0);
  padding: 16px 32px;
  width: max-content;
  font-weight: 500;
  border: 0;
  display: block;
  font-size: 1.125rem;
  border-radius: 0;
  transition: 0.6s;
  overflow: hidden;
  background-color: #222222;
  color: #ccc;
}
.btn:focus {
  outline: 0;
}
.btn:before {
  content: "";
  display: block;
  position: absolute;
  background: rgba(255, 255, 255, 0.5);
  width: 60px;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0.5;
  filter: blur(30px);
  transform: translateX(-100px) skewX(-15deg);
}
.btn:after {
  content: "";
  display: block;
  position: absolute;
  background: rgba(255, 255, 255, 0.2);
  width: 30px;
  height: 100%;
  left: 30px;
  top: 0;
  opacity: 0;
  filter: blur(5px);
  transform: translateX(-100px) skewX(-15deg);
}
.btn:hover {
  text-decoration: none;
  background: #e4a8ba;
  color: #222222;
  cursor: pointer;
}
.btn:hover:before {
  transform: translateX(300px) skewX(-15deg);
  opacity: 0.6;
  transition: 0.7s;
}
.btn:hover:after {
  transform: translateX(300px) skewX(-15deg);
  opacity: 1;
  transition: 0.7s;
}

input {
  font-size: 0.875rem;
  color: #999999;
  padding: 14px 16px;
  border: none;
}

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto;
}

.wrap {
  min-width: 320px;
  max-width: 1400px;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto;
}

.header__wrap {
  background-color: #F9F6F5;
  position: sticky;
  top: 0;
  z-index: 100;
  width: 100%;
  border-bottom: 1px solid #222222;
}

.header {
  color: #222222;
  display: grid;
  grid-template-columns: max-content auto max-content;
  justify-content: space-between;
  align-items: center;
  padding: 30px 20px;
}

.header__menu-mobile {
  display: none;
  z-index: 99999;
}

.header__logo {
  font-size: 1.5rem;
  position: relative;
  font-style: italic;
  font-weight: 700;
}
.header__logo a::after {
  content: "";
  background-color: #222222;
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  bottom: -5%;
}

.header__list {
  display: flex;
  gap: 40px;
}

.seach__form {
  position: relative;
}
.seach__form input {
  border: 1px solid #7e8185;
  padding: 5px;
  border-radius: 1.25rem;
}
.seach__form i {
  position: absolute;
  top: 25%;
  right: 8%;
  color: #7e8185;
}

.header__button {
  display: flex;
  gap: 24px;
  align-items: center;
}
.header__button i {
  font-size: 1.375rem;
}

.header__burger {
  display: none;
}

.header__burger.active::before {
  transform: rotate(45deg);
  width: 100%;
  right: 0;
  top: 5px;
  transition: all 0.3s ease 0s;
}

.header__burger.active::after {
  transform: rotate(-45deg);
  width: 100%;
  left: 0;
  bottom: 5px;
  transition: all 0.3s ease 0s;
}

.header__burger.active span {
  transform: scale(0);
  transition: all 0.3s ease 0s;
}

@media (max-width: 1024px) {
  .header {
    grid-template-columns: 114px auto max-content;
  }

  .header__menu {
    display: none;
  }

  .header__burger {
    display: block;
    position: relative;
    width: 18px;
    height: 12px;
    z-index: 3;
  }
  .header__burger span {
    position: absolute;
    background-color: #222222;
    left: 0;
    width: 100%;
    height: 2px;
    top: 5px;
    transition: all 0.3s ease 0s;
  }
  .header__burger:before {
    content: "";
    background-color: #222222;
    position: absolute;
    width: 65%;
    height: 2px;
    right: 10%;
    transition: all 0.3s ease 0s;
    top: 0;
  }
  .header__burger:after {
    content: "";
    background-color: #222222;
    position: absolute;
    width: 65%;
    height: 2px;
    left: 10%;
    transition: all 0.3s ease 0s;
    bottom: 0;
  }

  .header__menu-mobile {
    display: block;
    position: fixed;
    right: -100%;
    overflow: auto;
    width: 100%;
    height: 100%;
    background-color: #e4a8ba;
    padding: 30px;
    transition: all 0.5s ease 0s;
    max-height: 100vh;
  }

  .header__menu-mobile.active {
    right: 0;
    transition: all 0.5s ease 0s;
    margin-left: auto;
  }

  .header__list {
    display: block;
  }
}
@media (max-width: 500px) {
  .header {
    grid-template-columns: max-content auto max-content;
    grid-template-rows: repeat(2, 1fr);
    grid-row-gap: 20px;
  }

  .header__logo {
    grid-column: 1/-1;
    grid-row: 1/-2;
    width: max-content;
    margin: 0 auto;
  }

  .header__burger {
    grid-column: 1/2;
    grid-row: -2/-1;
  }

  .header__button {
    grid-column: -2/-1;
    grid-row: -2/-1;
  }
}
.offer__slide {
  position: relative;
}
.offer__slide .banner__img {
  width: 100%;
