}

.swiper-pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.swiper-pagination-bullet {
  height: 2px;
  width: 60px;
  border-radius: 0;
  background: #000;
  margin: 0 10px;
}

.swiper-pagination-bullet.swiper-pagination-bullet-active {
  height: 3px;
}

.swiper-pagination-horizontal span,
.swiper-pagination-horizontal span:active,
.swiper-pagination-horizontal span:focus {
  outline: none;
}

@media (min-width: 1700px) {
  .offer__slide .offer__slide-text {
    left: 15%;
  }
}
@media (max-width: 1024px) {
  .offer__slide {
    display: grid;
    grid-template-rows: max-content auto;
    align-items: flex-start;
  }
  .offer__slide img {
    object-position: center center;
    width: 100%;
    height: 100%;
  }
  .offer__slide .offer__slide-text {
    position: relative;
    top: 0;
    left: 0;
    padding: 60px 30px;
    text-align: center;
  }
  .offer__slide .offer__slide-text h2 {
    max-width: 100%;
    font-size: 2.25rem;
    line-height: 3.125rem;
  }
  .offer__slide .offer__slide-text p {
    max-width: 600px;
    font-size: 1.25rem;
    line-height: 1.75rem;
  }
  .offer__slide .offer__slide-text .btn {
    margin: 0 auto;
  }
}
