.stock__wrap {
    padding-top: 130px;
}

.stock__title-button {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.section__title {

    display: grid;
    gap: 20px;

    h2 {
        font-weight: 500;
        font-size: px-to-rem(56px);
        line-height: px-to-rem(70px);
    }

    p {
        font-size: px-to-rem(20px);
        line-height: px-to-rem(32px);
        max-width: 700px;
        color: $gray2;
    }
}

.stock__button {

    display: flex;
    gap: 8px;
    font-weight: 500;
    font-size: px-to-rem(24px);
    line-height: px-to-rem(30px);
    align-items: center;
    
    img {
        width: 16px;
        height: 16px;
    }
}

.stock__items {
    padding: 60px 0 0;
    display: grid;
    gap: 40px;
    grid-template-columns: 1fr 1fr;
}

.stock__item {
    display: grid;
    border: 1px solid rgba(104, 90, 82, 0.19);
    box-sizing: border-box;
}

.stock__item-img {
    max-width: 100%;

    img {
        width: 100%;
    }
}

.stock__item-content {
    padding: 32px;
    display: grid;
    gap: 47px;

    .stock__item-text {

        h3 {
            font-weight: 500;
            font-size: px-to-rem(34px);
            line-height: px-to-rem(60px);
            color: $font-color-brown;
        }

        p {
            font-size: px-to-rem(20px);
            line-height: px-to-rem(32px);
            color: $gray2;
        }
    }

    .stock__item-button-price {
        display: flex;
        justify-content: space-between;
        align-items: center;
    
        .stock__item-button {
            display: flex;
            gap: 24px;

            .btn-add-cart {
                @include button('','',true);
                padding: 16px;
            }
        }
        
        .stock__item-price {
            font-weight: 500;
            font-size: px-to-rem(30px);
            line-height: px-to-rem(32px);
        }
    }
}

@media (max-width:1024px) {

    .stock__wrap {
        padding-top: 100px;
    }

    .section__title {
    
        h2 {
            font-size: px-to-rem(36px);
            line-height: px-to-rem(50px);
        }

    }

    .stock__title-button {
        display: grid;
        grid-template-columns: repeat(2,1fr);
        grid-template-rows: 1fr auto;
        row-gap: 20px;

        .section__title {
            grid-column: 1 / -1;
        }
        .stock__button {
            grid-column: 1 / -1;
            justify-content: right;
        }
    }

    .stock__item-content {
        padding: 25px;
        gap: 25px;

        .stock__item-text {

            h3 {
                font-size: px-to-rem(28px);
                line-height: px-to-rem(52px);
            }

            p {
                font-size: px-to-rem(18px);
                line-height: px-to-rem(28px);
            }
        }

        .stock__item-button-price {
        
            .stock__item-button {
                display: flex;
                gap: 20px;

                .btn , .btn-add-cart {
                    padding: 14px;
                    font-size: px-to-rem(16px);
                }
            }
            
            .stock__item-price {
                font-size: px-to-rem(26px);
                line-height: px-to-rem(28px);
            }
        }
    }
}

@media (max-width:680px) {
    
    .stock__items {
        gap: 24px;
        grid-template-columns: 1fr;
    }
}
