.reviews__wrap {
    padding: 130px 0;
}

.reviews__slide-content {
    display: grid;
    grid-template-columns: (max-content) (max-content);
    justify-content: center;
    gap: 40px;
    align-items: center;
    background-color: $white;
    margin-bottom: 60px;
}

.reviews__slide-img {
    width: 500px;
    height: auto;
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.reviews__slide-text {
    max-width: 625px;
    display: grid;
    gap: 60px;
    align-content: start;

    h3 {
        font-weight: 500;
        font-size: px-to-rem(30px);
        line-height: px-to-rem(40px);
    }

    p {
        font-size: px-to-rem(20px);
        line-height: px-to-rem(32px);
    }

}

.reviews__slide-person {
    display: grid;
    gap: 10px;

    h4 {
        font-weight: 500;
        font-size: px-to-rem(30px);
        line-height: px-to-rem(40px);
    }

    p {
        font-size: px-to-rem(20px);
        line-height: px-to-rem(24px);
    }
}

@media (max-width:1200px) {

    .reviews__slide-img {
        width: 400px;
    }
    
    .reviews__slide-text {
        max-width: 500px;
        gap: 30px;
    }
}

@media (max-width:1024px) {

    .reviews__slide-content {
        grid-template-columns: auto;
    }

    .reviews__slide-img {
        width: 450px;
        margin: 0 auto;
    }

    .reviews__slide-text {
        text-align: center;
        max-width: 80%;
        margin: 0 auto;

        h3 {
            font-weight: 500;
            font-size: px-to-rem(30px);
            line-height: px-to-rem(40px);
        }
    
        p {
            font-size: px-to-rem(20px);
            line-height: px-to-rem(32px);
        }
    
    }
    
}

@media (max-width:500px) {

    .reviews__slide-img {
        width: 270px;
    }
    
}