.about__wrap {
    padding-top: 130px;
}

.about {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 40px;
    align-items: center;
}

.about__content {
    padding: 32px 0;
    max-width: 608px;
    display: grid;
    gap: 50px;
    height: max-content;

}

.about__content-text {
    display: grid;
    gap: 20px;

    h2 {
        font-weight: 500;
        max-width: 520px;
        font-size: px-to-rem(56px);
        line-height: px-to-rem(70px);
    }

    p {
        font-size: px-to-rem(20px);
        line-height: px-to-rem(32px);
        color: $gray2;
    }
}

.about__img {
    width: 100%;
    height: 100%;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

@media (max-width:1024px) {

    .about__wrap {
        padding-top: 100px;
    }

    .about {
        display: grid;
        grid-template-columns: 1fr;
        align-items: center;
    }

    .about__content {
        padding: 0 30px;
        text-align: center; 
        margin: 0 auto;
        max-width: 700px;

        
        .btn {
            margin: 0 auto;
        }
    }

    .about__content-text {
        h2 {
            max-width: 100%;
            font-size: px-to-rem(36px);
            line-height: px-to-rem(50px);
        }

    }
    
}