.header__wrap {
    background-color: $bg-color;
    position: sticky;
    top: 0;
    z-index: 100;
    width: 100%;
    border-bottom: 1px solid $font-color-brown;

}

.header {
    color: $font-color-brown;
    display: grid;
    grid-template-columns: (max-content) auto (max-content);
    justify-content: space-between;
    align-items: center;
    padding: 30px 20px;
}

.header__menu-mobile {
    display: none;
    z-index: 99999;
}

.header__logo {
    font-size: px-to-rem(24px);
    position: relative;
    font-style: italic;
    font-weight: 700;

    a::after {
        content: '';
        background-color: $font-color-brown;
        position: absolute;
        width: 100%;
        height: 2px;
        left: 0;
        bottom: -5%;
    }
}

.header__list {
    display: flex;
    gap: 40px;
}

.seach__form {
    position: relative;
    input {
        border: 1px solid #7e8185;
        padding: 5px;
        border-radius: 1.25rem;
    }
    i {
        position: absolute;
        top: 25%;
        right: 8%;
        color: #7e8185;
    }
}

.header__button {
    display: flex;
    gap: 24px;
    align-items: center;
    i {
        font-size: px-to-rem(22px);
    }
}

.header__burger {
    display: none;
}

.header__burger.active::before {
    transform: rotate(45deg);
    width: 100%;
    right: 0;
    top: 5px;
    transition: all 0.3s ease 0s;
}

.header__burger.active::after {
    transform: rotate(-45deg);
    width: 100%;
    left: 0;
    bottom: 5px;
    transition: all 0.3s ease 0s;

}

.header__burger.active span {
    transform: scale(0);
    transition: all 0.3s ease 0s;

}

@media (max-width: 1024px) {

    .header {
        grid-template-columns: (114px) auto (max-content);
    }

    .header__menu {
        display: none;
    }

    .header__burger {
        display: block;
        position: relative;
        width: 18px;
        height: 12px;
        z-index: 3;
    
        span {
            position: absolute;
            background-color: $font-color-brown;
            left: 0;
            width: 100%;
            height: 2px;
            top: 5px;
            transition: all 0.3s ease 0s;
        }
    
        &:before {
            content: '';
            background-color: $font-color-brown;
            position: absolute;
            width: 65%;
            height: 2px;
            right: 10%;
            transition: all 0.3s ease 0s;
            top: 0;
        }
    
        &:after {
            content: '';
            background-color: $font-color-brown;
            position: absolute;
            width: 65%;
            height: 2px;
            left: 10%;
            transition: all 0.3s ease 0s;
            bottom: 0;
    
        }
    }

    .header__menu-mobile {
        display: block;
        position: fixed;
        right: -100%;
        overflow: auto;
        width: 100%;
        height: 100%;
        background-color: $light-pink;
        padding: 30px;
        transition: all 0.5s ease 0s;
        max-height: 100vh;
    }

    .header__menu-mobile.active {
        right: 0;
        transition: all 0.5s ease 0s;
        margin-left: auto;
    }

    .header__list {
        display: block;
    }
}

@media (max-width: 500px) {

    .header {
        grid-template-columns: (max-content) auto (max-content);
        grid-template-rows: repeat(2, 1fr);
        grid-row-gap: 20px;
    }

    .header__logo {
        grid-column: 1 / -1 ;
        grid-row: 1 / -2 ;
        width: max-content;
        margin: 0 auto;
    }

    .header__burger {
        grid-column: 1 / 2 ;
        grid-row: -2 / -1 ;
    }

    .header__button {
        grid-column: -2 / -1 ;
        grid-row: -2 / -1 ;
    }
}

